import { Component } from '@angular/core';
import { YoutobeAPIService } from './youtobe-api.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'exampleMaterial';
  selected = 5;
  keyAPI = "";
  debounce;
  toggeSetting = false;
  numbers = [5, 10, 15, 20];
  setting = {
    debounce: this.debounce,
    keyAPI: this.keyAPI,
    pageSize: this.selected,
  };
  constructor(private apiService: YoutobeAPIService) { }

  toggleSetting() {
    this.toggeSetting = !this.toggeSetting;
  }

  closed() {
    this.toggeSetting = false;
  }

  saveSetting() {
    this.setting = {
      debounce: this.debounce,
      keyAPI: this.keyAPI,
      pageSize: this.selected,
    };
    this.toggeSetting = false;
    sessionStorage.setItem('key', this.setting.keyAPI);
  }
}
