import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

const configHTTP = {
  url: 'https://www.googleapis.com/youtube/v3/search',
  key: 'AIzaSyAPNM_yK_l-kNqtnQ4lcRHgcEgcb6R8Vxo',
  part: 'snippet',
  type: 'video'
}

@Injectable({
  providedIn: 'root'
})
export class YoutobeAPIService {
  dataItem;
  setting;
  constructor(private http: HttpClient) { }

  getDataApi(value, page, key) {
    return this.http.get(configHTTP.url, {
      params: {
        key: key,
        part: configHTTP.part,
        type: configHTTP.type,
        q: value,
        maxResults: page,
      }
    });
  }

  sortDataYoutobe(value, page, key, order) {
    return this.http.get(configHTTP.url, {
      params: {
        key: key,
        part: configHTTP.part,
        type: configHTTP.type,
        q: value,
        maxResults: page,
        order: order
      }
    });
  }

  getDataApiNext(value, nextPageToken, page, key) {
    return this.http.get(configHTTP.url, {
      params: {
        key: key,
        part: configHTTP.part,
        type: configHTTP.type,
        q: value,
        pageToken: nextPageToken,
        maxResults: page,
      }
    }).toPromise();
  }
  
  getDataApiPre(value, prevPageToken, page, key) {
    return this.http.get(configHTTP.url, {
      params: {
        key: key,
        part: configHTTP.part,
        type: configHTTP.type,
        q: value,
        pageToken: prevPageToken,
        maxResults: page,
      }
    }).toPromise();
  }

  getDataItem(id, key) {
    return this.http.get('https://www.googleapis.com/youtube/v3/videos', {
      params: {
        key: key,
        part: 'snippet',
        id: id,
      }
    });
  }

  getView(id, key) {
    return this.http.get('https://www.googleapis.com/youtube/v3/videos', {
      params: {
        key: key,
        part: 'statistics',
        id: id,
      }
    });
  }
}
