import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { YoutobeAPIService } from '../youtobe-api.service';
import { fromEvent } from "rxjs";
import { debounceTime, map } from "rxjs/operators";
import { Router } from '@angular/router';
import {FormControl} from '@angular/forms';

@Component({
  selector: 'app-search-video',
  templateUrl: './search-video.component.html',
  styleUrls: ['./search-video.component.css']
})
export class SearchVideoComponent implements OnInit {
  @ViewChild("search", { static: true }) search: ElementRef<HTMLInputElement>;
  @Input() setting;
  date1 = new FormControl(new Date());
  date2 = new FormControl(new Date());
  toggle = true;
  listItem = [];
  dataItem;
  title = 'exampleMaterial';
  panelOpenState = false;
  value = '';
  nextToken = '';
  preToken = '';
  length = 99;
  pageSize = 5;
  keyAPI;
  pageSizeOptions: number[] = [5, 10, 15, 20];
  listSort = ['date', 'rating', 'relevance', 'title', 'videoCount', 'viewCount']
  constructor(private apiService: YoutobeAPIService, private router: Router) {
   this.keyAPI =  this.keyAPI = sessionStorage.getItem("key");
  }

  ngOnInit() {
    
    fromEvent(this.search.nativeElement, "keyup")
      .pipe(
        debounceTime(this.setting.debounce ? this.setting.debounce : 500),
        map((event: any) => event.target.value)
      )
      .subscribe(val => {
        this.pageSize = this.setting.pageSize ? this.setting.pageSize : 5;
        this.router.navigate([""]);
        this.value = val;
        this.toggle = true;
        this.keyAPI = sessionStorage.getItem("key");
        this.apiService.getDataApi(val, this.setting.pageSize, this.keyAPI).subscribe((data) => {
          if (data) {
            this.value = val;
            this.nextToken = data['nextPageToken'];
            this.listItem = data['items'];
            this.length = data['pageInfo'].totalResults;
          }
        }, err => {
          if(err.status === 400){
            alert("bạn chưa nhập key hoặc key bạn sai");
          } else {
            alert("key bạn het han");
          }
        }
        );
      });
  }
  async nextPage(event) {
    this.pageSize = event.pageSize;
    if (event.pageIndex > event.previousPageIndex) {
      let data = await this.apiService.getDataApiNext(this.value, this.nextToken, this.pageSize, this.keyAPI);
      this.listItem = data['items'];
      this.nextToken = data['nextPageToken'];
      this.preToken = data['prevPageToken'];
    } else if (this.preToken) {
      let data = await this.apiService.getDataApiPre(this.value, this.preToken, this.pageSize, this.keyAPI);
      this.listItem = data['items'];
      this.nextToken = data['nextPageToken'];
      this.preToken = data['prevPageToken'];
    } else {
       this.apiService.getDataApi(this.value, this.pageSize, this.setting.keyAPI).subscribe(data=>{
          this.listItem = data['items'];
       });
    }
  }

  sortData(value) {
    this.apiService.sortDataYoutobe(this.value, this.pageSize, this.setting.keyAPI, value).subscribe((data) => {
      this.listItem = data['items'];
      console.log(data);
    })
  }
}
